.App {
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.map {
    z-index: 2;
    width: 100vw;
    height: 100vh;
}

.filterPanel {
    position: absolute;
    z-index: 3;
    top: 120px;
    left: 10px;
    overflow-y: scroll;
    max-width: 300px;
    height: calc(100% - 205px);
    background: rgba(100, 100, 100, .85);
}

.filterPanel input {
    font-size: 14px;
    width: 41%;
    height: 75px;
    margin: 10px;
    cursor: pointer;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    vertical-align: top;
    white-space: normal;
    color: white;
    border: none;
}

.filterPanel .selected {
    opacity: 1;
    background: rgb(20, 150, 250);
}

.closePanel {
    left: -300px;
}

.title {
    font-size: 1.1em;
    margin: 20px auto 5px;
    text-align: center;
    color: white;
}

.leaflet-container a.leaflet-popup-close-button {
    font: 40px/30px Tahoma, Verdana, sans-serif;
    right: 12px;
}

.filterInfo {
    font-size: 16px;
    position: absolute;
    z-index: 3;
    bottom: 5px;
    left: 10px;
    width: 280px;
    padding: 10px;
    text-align: center;
    color: white;
    border-radius: .3rem;
    background: rgba(90, 90, 90, .9);
}

hr {
    width: 200px;
    margin: 20px auto;
}

.externalLinkButton {
    display: inline;
}


/*****BUTTONS*****/

.leaflet-container .popupPhone {
    display: inline-block;
    padding: 10px;
    text-decoration: none;
    color: white;
    border-radius: .3rem;
    background: rgb(20, 150, 250);
}

.externalLinkButton a {
    text-decoration: none;
}

.blueButton {
    opacity: .8;
    background: rgb(52, 111, 140);
}

.redButton {
    background: rgb(204, 45, 50);
}

.purpleButton {
    background: rgb(129, 82, 120);
}

.greenButton {
    background: rgb(20, 180, 70);
}

.togglePanelButton {
    font-size: 1em;
    position: absolute;
    z-index: 3;
    top: 65px;
    left: 10px;
    width: 250px;
    height: 50px;
    cursor: pointer;
    color: white;
    border: none;
    background: rgb(90, 90, 90);
}

.geoLocateButton {
    position: absolute;
    z-index: 3;
    top: 10px;
    left: 10px;
    display: -ms-flexbox;
    display: flex;
    width: 250px;
    height: 50px;
    border-radius: .3rem;
    background: rgba(100, 100, 100, .8);
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.geoLocateButton input {
    width: 100%;
    height: 100%;
    cursor: pointer;
    color: white;
    border: none;
    background: rgb(226, 70, 70);
}

input {
    border-radius: .3rem;
}

/*END BUTTONS*/

.hideTopButtons {
    display: none;
}


/******Responsiveness*****/

@media (min-width: 600px) {
    .geoLocateButton {
        right: 50px;
        left: auto;
    }
    .geoLocateButton input {
        font-size: 16px;
    }
    .togglePanelButton {
        top: 10px;
    }
    .externalLinkButton {
        display: inline;
    }
    .filterPanel {
        top: 65px;
        bottom: auto;
        -webkit-transition: .15s;
        -o-transition: .15s;
        transition: .15s;
        height: calc(100% - 150px);
    }
    .filterPanel .externalLinkButton input {
        display: inline;
        width: 41%;
        height: 75px;
        padding: 10px;
        vertical-align: top;
        white-space: normal;
    }
    .filterPanel input {
        font-size: 16px;
        font-size: 15px;
        display: block;
        width: 250px;
        height: 50px;
    }
    .title {
        font-size: 1.3em;
        margin: 20px auto 10px;
    }
    .closePanel {
        left: -300px;
    }
    .filterPanel .selected {
        margin-left: 25px;
    }
    .filterInfo {
        left: 10px;
    }
}


/*Loading*/

.sk-fading-circle {
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 50%;
    width: 40px;
    height: 40px;
    -webkit-transform: translate(, -50%, -50%);
        -ms-transform: translate(, -50%, -50%);
            transform: translate(, -50%, -50%);
}

.sk-fading-circle .sk-circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.sk-fading-circle .sk-circle:before {
    display: block;
    width: 15%;
    height: 15%;
    margin: 0 auto;
    content: '';
    -webkit-animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
    animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
    border-radius: 100%;
    background-color: #333;
}

.sk-fading-circle .sk-circle2 {
    -webkit-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
    transform: rotate(30deg);
}

.sk-fading-circle .sk-circle3 {
    -webkit-transform: rotate(60deg);
    -ms-transform: rotate(60deg);
    transform: rotate(60deg);
}

.sk-fading-circle .sk-circle4 {
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
}

.sk-fading-circle .sk-circle5 {
    -webkit-transform: rotate(120deg);
    -ms-transform: rotate(120deg);
    transform: rotate(120deg);
}

.sk-fading-circle .sk-circle6 {
    -webkit-transform: rotate(150deg);
    -ms-transform: rotate(150deg);
    transform: rotate(150deg);
}

.sk-fading-circle .sk-circle7 {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}

.sk-fading-circle .sk-circle8 {
    -webkit-transform: rotate(210deg);
    -ms-transform: rotate(210deg);
    transform: rotate(210deg);
}

.sk-fading-circle .sk-circle9 {
    -webkit-transform: rotate(240deg);
    -ms-transform: rotate(240deg);
    transform: rotate(240deg);
}

.sk-fading-circle .sk-circle10 {
    -webkit-transform: rotate(270deg);
    -ms-transform: rotate(270deg);
    transform: rotate(270deg);
}

.sk-fading-circle .sk-circle11 {
    -webkit-transform: rotate(300deg);
    -ms-transform: rotate(300deg);
    transform: rotate(300deg);
}

.sk-fading-circle .sk-circle12 {
    -webkit-transform: rotate(330deg);
    -ms-transform: rotate(330deg);
    transform: rotate(330deg);
}

.sk-fading-circle .sk-circle2:before {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s;
}

.sk-fading-circle .sk-circle3:before {
    -webkit-animation-delay: -1s;
    animation-delay: -1s;
}

.sk-fading-circle .sk-circle4:before {
    -webkit-animation-delay: -.9s;
    animation-delay: -.9s;
}

.sk-fading-circle .sk-circle5:before {
    -webkit-animation-delay: -.8s;
    animation-delay: -.8s;
}

.sk-fading-circle .sk-circle6:before {
    -webkit-animation-delay: -.7s;
    animation-delay: -.7s;
}

.sk-fading-circle .sk-circle7:before {
    -webkit-animation-delay: -.6s;
    animation-delay: -.6s;
}

.sk-fading-circle .sk-circle8:before {
    -webkit-animation-delay: -.5s;
    animation-delay: -.5s;
}

.sk-fading-circle .sk-circle9:before {
    -webkit-animation-delay: -.4s;
    animation-delay: -.4s;
}

.sk-fading-circle .sk-circle10:before {
    -webkit-animation-delay: -.3s;
    animation-delay: -.3s;
}

.sk-fading-circle .sk-circle11:before {
    -webkit-animation-delay: -.2s;
    animation-delay: -.2s;
}

.sk-fading-circle .sk-circle12:before {
    -webkit-animation-delay: -.1s;
    animation-delay: -.1s;
}

@-webkit-keyframes sk-circleFadeDelay {
    0%,
    39%,
    100% {
        opacity: 0;
    }
    40% {
        opacity: 1;
    }
}

@keyframes sk-circleFadeDelay {
    0%,
    39%,
    100% {
        opacity: 0;
    }
    40% {
        opacity: 1;
    }
}

#google_translate_element {
    position: absolute;
    z-index: 1000;
    top: 10px;
    left: 10px;
}



